import React, { useState } from 'react';
import Input from '../Input/Input';
import Back from '../Back/Back';
import UploadFile from './UploadFilesSection/UploadFilesPart';
import './Upload.css';
import axios from 'axios';
import Header from '../Header/Header';

const Upload = () => {
    return (
        <div>
            <Header />
            <div className='upload'>
            <Back />
            <UploadFile limitFile="5" limitImage="100" />
            <div className='metadataSection'>
                <div>
                    <Input label="" placeholder=" "/>
                    <div className='metadataLabel'></div>
                    <select className="visible" id='metadataField'>
                        <option value="Private" selected></option>
                        <option value="Public"></option>
                    </select>
                    <Input label="" placeholder=" "/>
                    <Input label="" placeholder=" "/>
                    <Input label=" " placeholder="  "/>
                </div>
                <div>
                    <Input label="" placeholder=" " />
                    <div id='descriptionInputContainer'>
                        <div className='metadataBigItem'>
                            <div className='inputLabel'></div>
                            <textarea id='descriptionInput' placeholder=' '></textarea>
                        </div>
                    </div>
                    <div id='saveButtons'>
                        <button className='saveDraft'  onClick={()=>{
                                axios.get('https://tab-with-datasets-test2.onrender.com/', {})
                                    .then(response => {
                                        alert(response.data);
                                    })
                                    .catch((error) => {
                                      console.log(error);
                                    });
                            }}> </button>
                            <button className='uploadDataset' onClick={()=>{
                                axios.get('https://tab-with-datasets-mock.onrender.com/upload', {})
                                    .then(response => {
                                        alert(response.data);
                                    })
                                    .catch((error) => {
                                      console.log(error);
                                    });
                            }}> </button>
                    </div>
                </div>
            </div>
        </div>
        </div>
    );
};

export default Upload;
